.item4{
    border-top: 1px solid rgb(25, 175, 245);
}

.item4 h3{
    display: flex;
    justify-content: center;
    color: white;
    margin: 0;
    margin-top: 10px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.item4 .contact-component ul{
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.item4 .contact-component ul li{
    list-style: none;
    margin: 5px;
}

.item4 .contact-component ul li a{
    text-decoration: none;
    color: black;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.item4 .contact-component ul li a .contact-icon{
    margin-right: 20px;
    font-size: 30px;
}
.item4 .contact-component ul li .contact-icon{
    margin-right: 20px;
    font-size: 30px;
}
.item4 .contact-component ul li a #facebook{
    color: blue;
}

.item4 .contact-component ul li  #whatsapp{
    color: green;
    cursor: pointer;
}