.container{
    background: linear-gradient(rgb(34, 186, 233) ,rgb(115, 209, 226));
    display: grid;
    grid-template-rows: 0.5fr 5fr 1fr 1fr;
    height: 100vh;
}

.item1{
    display: flex;
    justify-content: center;
    color: white;
}
.item3 input{
    width: 400px;
    border-radius: 10px;
    border: none;
    outline: none;
    font-size: 18px;
    padding: 10px;
    display: flex;
    flex-wrap: wrap;
    font-family: Georgia, 'Times New Roman', Times, serif;
    box-shadow: -5px 2px 10px 7px rgb(25, 175, 245);
}
.item3-parent{
    display: flex;
    justify-content: center;
    flex-wrap: nowrap;
    gap: 20px;
}
.item3 button{
  background-color: rgb(226, 100, 220);
  color: white;
  font-size: 20px;
  width: 90px;
  border: none;
  border-radius: 1rem;
}

.item2{
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Gill Sans', 'Gill Sans MT',  'Trebuchet MS', sans-serif;
}

.item2 .text{
width: 300px;
font-family: Georgia, 'Times New Roman', Times, serif;
color: aliceblue;
}

@media  screen and (max-width: 580px) {
    .item3 input{
        width: 220px;
    }
    .item4 .contact-component ul{
        position: relative;
        right: 20px;
    }
    .item3 button{
        background-color: rgb(226, 100, 220);
        box-shadow: -5px 2px 10px 7px rgb(25, 175, 245);
        color: white;
        font-size: 20px;
        height: 30px;
        position: relative;
        top: 11.5px;
        border: none;
        border-radius: 1rem;
      }
}